import React from "react";

import "./Navbar.scss";

import { NavLink, useRouteMatch } from "react-router-dom";

const Navbar: React.FC = () => {
  const matchChapterScreen = useRouteMatch("/chapters");
  const matchHomesScreen = useRouteMatch("/");
  const matchCharacterScreen = useRouteMatch("/characters");
  const matchAboutScreen = useRouteMatch("/about");

  if (
    !matchChapterScreen?.isExact &&
    !matchHomesScreen?.isExact &&
    !matchCharacterScreen?.isExact &&
    !matchAboutScreen?.isExact
  ) {
    return <div></div>;
  }

  return (
    <nav>
      <div className="Navbar">
        <ul className="nav-links">
          <NavLink
            activeClassName="nav-active"
            to="/"
            className="nav-link-i"
            exact
          >
            <li>Home</li>
          </NavLink>
          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/chapters"
          >
            <li>Chapters</li>
          </NavLink>

          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/characters"
          >
            <li>Characters</li>
          </NavLink>

          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/about"
          >
            <li>About</li>
          </NavLink>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
